</template>
<style>
#app{
  font-family: "微软雅黑";
  margin:0;
  padding: 0;
  width:100%;
  height:100%;
}
.main{
  float:left;
  width:95%;
  height: 100%;
  background-color: #EFF2F7;
  overflow: hidden;
}
</style>

<script>
import sideBar from './components/common/sideBar.vue'
export default {
  name: 'App',
  components:{
    'side-bar':sideBar
  }
}
</script>




