<template>
	<div class="pos">
		<el-row>
			<el-col :span='7' class='pos-left' id='pos-left'>
				<el-tabs>
					<el-tab-pane label='点餐'>
						<el-table :data='tableData' border style="width: 100%">
							<el-table-column prop='goodsName' label='商品'></el-table-column>
							<el-table-column prop='count' label='数量'></el-table-column>
							<el-table-column prop='price' label='单价'></el-table-column>
							<el-table-column  fixed='right' label="操作">
								<template slot-scope='scope'>
									<el-button type='text' size='small' @click="delateSingleGoods(scope.row)">删除</el-button>
									<el-button type='text' size='small' @click="addOrderList(scope.row)">增加</el-button>
								</template>	
							</el-table-column>
						</el-table>
						<div class="total">
							<small>数量：</small>{{totalCount}} &nbsp;&nbsp;&nbsp;&nbsp; <small>金额：</small>{{totalMoney}}元
						</div>
						<div  class='pos-btn'>
							<el-button type="warning">挂单</el-button>
							<el-button type="danger" @click="delateAllGoods">删除</el-button>
							<el-button type="success" @click="checkout">结账</el-button>
						</div>
					</el-tab-pane>
					<el-tab-pane label='挂单'></el-tab-pane>
					<el-tab-pane label='外卖'></el-tab-pane>
				</el-tabs>
			</el-col>
			<el-col :span='17' class='pos-right'>
				<div class="often-goods">
					<div class="title">常用商品</div>
					<div class="often-goods-list">
						<ul>
							<li v-for="goods in oftenGoods" @click="addOrderList(goods)"><span>{{goods.goodsName}}</span>
								<span class="price">￥{{goods.price}}</span>
							</li>
						</ul>
					</div>
				</div>

				<div class="goods-type">
					<el-tabs>
						<el-tab-pane label="汉堡">
							<ul class="cookList">
								<li v-for='goods in goods0' @click="addOrderList(goods)">
									<span class="foodImg"><img v-bind:src="goods.goodsImg" width="100%"></span>
									<span class="foodName">{{goods.goodsName}}</span>
									<span class="foodPrice">￥{{goods.price}}</span></li>
								</ul>
							</el-tab-pane>
							<el-tab-pane label="小食">
								<ul class="cookList">
								<li v-for='goods in goods1' @click="addOrderList(goods)">
									<span class="foodImg"><img v-bind:src="goods.goodsImg" width="100%"></span>
									<span class="foodName">{{goods.goodsName}}</span>
									<span class="foodPrice">￥{{goods.price}}</span></li>
								</ul>
							</el-tab-pane>
							<el-tab-pane label="饮料">
								<ul class="cookList">
								<li v-for='goods in goods2' @click="addOrderList(goods)">
									<span class="foodImg"><img v-bind:src="goods.goodsImg" width="100%"></span>
									<span class="foodName">{{goods.goodsName}}</span>
									<span class="foodPrice">￥{{goods.price}}</span></li>
								</ul>
							</el-tab-pane>
							<el-tab-pane label="套餐"><ul class="cookList">
								<li v-for='goods in goods3' @click="addOrderList(goods)">
									<span class="foodImg"><img v-bind:src="goods.goodsImg" width="100%"></span>
									<span class="foodName">{{goods.goodsName}}</span>
									<span class="foodPrice">￥{{goods.price}}</span></li>
								</ul></el-tab-pane>
						</el-tabs>
					</div>
				</el-col>
			</el-row>
		</div>
	</template>

	<style >
	.pos-left{
		text-align: center;
		background-color: #f9fafc;
		border-right: 1px solid #c0ccda;
	}
	.pos-right{
		text-align: center;
	}
	.pos-btn{
		margin-top: 10px;
	}
	.title{
		height:20px;
		border-bottom: 1px solid #e1e1e1;
		background-color: #F9FAFC;
		padding:10px;
		text-align: left;
	}
	.often-goods-list{
		overflow: hidden;
		background-color: #F9FAFC;
	}
	.often-goods-list ul li{
		list-style: none;
		float:left;
		border:1px solid #E5E9F2;
		padding:10px;
		margin:10px;
		background-color: #FFF;
		cursor:pointer;
	}
	.price{
		color:#5887FF;
	}

	.goods-type{
		clear:both;
	}

	.cookList li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
       cursor: pointer;
 
   }
   .cookList li span{
       
        display: block;
        float:left;
   }
   .foodImg{
       width: 40%;
   }
   .foodName{
       font-size: 18px;
       padding-left: 10px;
       color:brown;
 
   }
   .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }
   .total{
   	background-color: #fff;
   	padding: 10px;
   	border-bottom: 1px solid #e1e1e1;
   	margin:10px;
   }
	</style>

	<script>
	import axios from 'axios'
	export default{
		name:'pos',
		data(){
			return{
				totalCount:0,
				totalMoney:0,
				tableData: [],
				oftenGoods:[/*{
					goodsName:"香辣鸡腿堡",
					goodsId:1,
					price:18
				},{
					goodsName:'田园鸡腿堡',
					goodsId:2,
					price:12
				},
				{
					goodsName:'和风汉堡',
					goodsId:3,
					price:17
				},{
					goodsName:'大包薯条',
					goodsId:4,
					price:18
				},{
					goodsName:'脆皮炸鸡腿',
					goodsId:5,
					price:20
				},{
					goodsName:'魔法鸡块',
					goodsId:6,
					price:21
				},{
					goodsName:'可口可乐',
					goodsId:7,
					price:18
				},{
					goodsName:'雪顶咖啡',
					goodsId:8,
					price:7
				},{
					goodsName:'儿童欢乐套餐',
					goodsId:9,
					price:25
				},{
					goodsName:'快乐全家桶',
					goodsId:10,
					price:99
				}*/],
				 goods0:[],
				 goods1:[],
				 goods2:[],
				 goods3:[]			}
		},
		created(){
			axios.get("http://jspang.com/DemoApi/oftenGoods.php")
			.then(response=>{
				console.log(response);
				this.oftenGoods=response.data;
			})
			.catch(error=>{
				console.log(error);
				alert('网络错误，不能访问')
			}),
			axios.get('http://jspang.com/DemoApi/typeGoods.php')
      .then(response=>{
         console.log(response);
         //this.oftenGoods=response.data;
         this.goods0=response.data[0];
         this.goods1=response.data[1];
         this.goods2=response.data[2];
         this.goods3=response.data[3];
 
      })
      .catch(error=>{
          console.log(error);
          alert('网络错误，不能访问');
      })
		},
		mounted:function(){
			var orderHeight=document.body.clientHeight;
			console.log(orderHeight);
			document.getElementById('pos-left').style.height=orderHeight+'px';
		},
		methods:{
			addOrderList(goods){
				//商品是否存在于订单列表中
				let isHave=false;
				for(let i=0;i<this.tableData.length;i++){
					if(this.tableData[i].goodsId==goods.goodsId){
						isHave=true;
					}
				}
				//根据判断的值编写业务逻辑
				if(isHave){
					//改变列表商品数量
					let arr=this.tableData.filter(o=>o.goodsId==goods.goodsId);
					arr[0].count++;
					console.log(arr[0].count);
				}else{
					let newGoods={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1};
					this.tableData.push(newGoods);
				}

				this.getTotal();
			},
			//删除单个商品
			delateSingleGoods(goods){
				this.tableData=this.tableData.filter(el=>el.goodsId!=goods.goodsId);
				this.getTotal();
			},
			//删除全部商品
			delateAllGoods(){
				this.tableData=[];
				this.totalMoney=0;
				this.totalCount=0;
			},
			//模拟结账
			checkout(){
				if(this.totalCount!=0){
					this.tableData=[];
					this.totalCount=0;
					this.totalMoney=0;
					this.$message({
						message:'结账成功！感谢惠顾',
						type:"success"
					});
				}else{
					this.$message.error("请加入商品");
				}
			},
			//汇总数量和金额
			getTotal(){
				this.totalMoney=0;
				this.totalCount=0;
				if(this.tableData){
					this.tableData.forEach((element)=>{
					this.totalCount=this.totalCount+element.count;
					this.totalMoney=this.totalMoney+(element.price*element.count);
				})
				}
			}
		}
	}
	</script>


