import Vue from 'vue'
import Router from 'vue-router'
import pos from '@/components/pages/pos.vue'

Vue.use(Router)

export default new Router({
  routes: [
  		{
    	path:'/pos',
    	name:'pos',
    	component:pos
    },
    {
    	path:'',
    	redirect:'/pos'
    }
  ]
})



